@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
@import url(https://fonts.googleapis.com/css?family=Lora);
:root {
  --lavender: rgb(202, 203, 213);
  --green: rgb(211, 212, 197);
  --cloud: rgb(223, 227, 230);
  --grey: rgb(219, 217, 219);
  --granite: rgb(101, 110, 121);
}

html,
body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

hr {
  border-top: 1px solid rgb(101, 110, 121);
  border-top: 1px solid var(--granite);
  border-bottom: 1px solid transparent;
}

.icon {
  fill: red;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Abril Fatface';
  color: rgb(101, 110, 121);
  color: var(--granite);
  font-size: 2.5vw;
}

h2 {
  padding: 3%;
}

h3 {
  font-size: 1.5vw;
  letter-spacing: 1px;
}

h4 {
  padding: 3%;
  font-style: italic;
  font-size: 16pt;
}

p {
  font-family: 'Lora';
  font-size: 1.1vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
  position: fixed;
  height: 5vh;
  left: 0;
  top: 0;
  background-color: rgba(202, 203, 213, 0.8);
  border-radius: 0px;
  width: 40vw;
  text-align: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  -webkit-transition: top 0.3s;
  -webkit-transition: top 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: top 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  z-index: 1000;
}

.navbar:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.navbar:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 54vw;
  height: 5vh;
  margin-top: -0.2vh;
  margin-right: -0.1vh;
  background: var(--lavender);
  -webkit-transform-origin: 100% 100% 0;
  transform-origin: 100% 100% 0;
  -webkit-transform: skew(-6deg);
  transform: skew(-6deg);
  z-index: -1;
}

.navbar > a {
  margin-top: 1vh;
  float: left;
  margin-left: 2vw;
  font-family: 'Abril Fatface';
  font-size: 1.3vw;
  font-style: italic;
  color: var(--granite);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.navbar > a:hover {
  text-decoration: none;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  opacity: 0.7;
}

.navbar > a:first-child {
  margin-left: 5vw;
}

.hideNavbar {
  top: -5vh;
}

.startPageWrapper {
  height: 100vh;
  background: url(/agi-p2-web/static/media/lake.84aa0462.jpg) no-repeat center center fixed;
  background-color: black;
  background-size: cover;

  display: block;
  text-align: center;

  position: relative;

  max-width: 100%;
  overflow-x: hidden;
}

.headerWrapper {
  display: inline-block;
  margin: auto;
  margin-top: 45vh;
}

.headerWrapper h2 {
  margin: 0;
  color: lightgrey;
  font-size: 65pt !important;
  letter-spacing: 5px;
}

.scrollWrapper {
  margin: auto;
  position: absolute;
  bottom: 50px;
  left: 50%;
}

.icon-scroll,
.icon-scroll:before {
  position: absolute;
  left: 50%;
  top: 80%;
}

.icon-scroll {
  width: 40px;
  height: 70px;
  margin-left: -20px;
  top: 50%;
  margin-top: -35px;
  box-shadow: inset 0 0 0 2px #fff;
  opacity: 0.8;
  border-radius: 25px;
}

.icon-scroll:before {
  content: '';
  width: 8px;
  height: 8px;
  background: white;
  margin-left: -4px;
  top: 8px;
  border-radius: 4px;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: scroll;
          animation-name: scroll;
}

@-webkit-keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(46px);
            transform: translateY(46px);
  }
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(46px);
            transform: translateY(46px);
  }
}

.aboutParallax {
  position: relative;
  height: 100vh;
  min-height: 100vh;
  width: 100vw;
  min-width: 100vw;
}

.aboutParallax > img {
  position: absolute;
  margin-left: -10%;
  min-height: 100%;
  min-width: 2000px;
}

.aboutContentWrapper {
  height: 100vh;
  width: 45vw;
  position: absolute;
  right: 0px;
  z-index: 10;
  background-color: var(--green);
}

.aboutContentWrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--green);
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: skew(-4deg);
  transform: skew(-4deg);
  z-index: -1;
}

.aboutContent {
  padding: 12%;
}

.aboutCard {
  position: relative;
  width: 110%;
  margin-left: -10%;
  margin-top: 8%;
  padding: 2%;
  border-radius: 5px;
  -webkit-transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.aboutCard > p,
.aboutCard > h3 {
  margin-left: 20%;
  margin-top: 1% !important;
  margin-bottom: 1% !important;
  text-align: left;
  -webkit-transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.aboutCard > img {
  position: absolute;
  width: 4vw;
  height: auto;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  margin-left: 2%;

  -webkit-transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);

  transition: all 0.9s cubic-bezier(0.25, 0.8, 0.25, 1);
}

#inverseCard {
  margin-left: -8%;
}

#inverseCard > p,
#inverseCard > h3 {
  margin-right: 20%;
  margin-left: 0% !important;
  text-align: right !important;
}

#inverseCard > img {
  right: 0;
  left: 85%;
}

.aboutCard:hover > p,
.aboutCard:hover > h3 {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.aboutCard:hover > img {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.aboutCard:hover {
  margin-left: -9%;
  margin-top: 9%;
  background: rgba(255, 255, 255, 0.1);
}

#smallerIcon {
  width: 3vw;
  height: auto;
}

.processPageWrapper {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: black;
}

.carousel-caption {
  bottom: 18%;
}

.carousel-caption > h3 {
  color: white;
  font-size: 40pt;
}

.processParallax {
  position: relative;
  height: 100vh;
  min-height: 100vh;
  width: 100vw;
  min-width: 100vw;
}

.teamPageWrapper {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.teamParallax {
  position: relative;
  height: 100vh;
  min-height: 100vh;
  width: 100vw;
  min-width: 100vw;
}

.teamDetailsPicture {
  position: relative;
  height: 100vh;
  min-height: 100vh;
  width: 100vw;
  min-width: 100vw;
}

.teamDetails {
  position: absolute;
  top: 0;
  left: 0;
  width: 60vw;
  height: 100vh;
  display: block;
  background: rgba(0, 0, 0, 0.75);
}
.teamDetailsPicture > img {
  position: absolute;
  left: 0;
  margin-top: 10%;
  margin-left: -2%;
  min-height: 130%;
  min-width: 70%;
}

.teamContentWrapper {
  height: 100vh;
  width: 45vw;
  position: absolute;
  right: 0px;
  z-index: 10;
  background-color: var(--lavender);
}

.teamContentWrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--lavender);
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: skew(-4deg);
  transform: skew(-4deg);
  z-index: -1;
}

.teamContent {
  padding: 10%;
}

.profileCard {
  margin-top: -4%;
}

.teamCard {
  position: relative;
  width: 110%;
  margin-left: -10%;
  margin-top: 13%;
}

.teamCard > p,
.teamCard > h3 {
  margin-left: 20%;
  margin-top: 1% !important;
  margin-bottom: 1% !important;
  text-align: left;
}

.teamCard > img {
  position: absolute;
  width: 80px;
  height: 80px;
  margin-right: 20%;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.profilesCard {
  /* display: flex; */
  padding-top: 5%;
  float:left;
  width: 40vw;
  height: 95vh;
}

.profilesList {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.profileCard {
  width: 30%;
  height: auto;
}

.profileCard > img {
  display: flex;
  border-radius: 50%;
  width: 100%;
  height: auto;
  margin: 10%;
  cursor: pointer;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.03);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.profileCard:hover > img {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
    box-shadow: 0 0px 30px rgba(0, 0, 0, 0.09);
}

.detailsCard {
  display: inline-block;
  height: 100vh;
}

.informationCard p {
  font-weight: bold;
  font-size: 24px;
}

.profileDetailsCard {
  padding: 5%;
  margin-right: 20%;
  text-align: left;
  color: rgb(202, 203, 213);
}

.profileDetailsNameRole {
  margin-top: -5%;
}

.profileDetailsDescription {
  margin-top: 10%;
  margin-right: 50%;
}

.profileDetailsLinks {
  position: absolute;
  bottom: 0;
  margin-bottom: 5%;
}

.profileDetailsLinks > a {
  padding-right: 20px;
}

.profileDetailsLinks > a > img {
  width: 80px;
  border-radius: 5%;
}

.profileDetailsCard .profileDetailsNameRole h2 {
  font-size: 55pt;
  letter-spacing: 1px;
}

.profileDetailsCard .profileDetailsNameRole h3 {
  font-size: 30pt;
  letter-spacing: 1px;
  margin-top: 0;
}

.profileDetailsCard .profileDetailsDescription h4 {
  font-style: normal;
  font-weight: lighter;
  font-size: 16pt;
}

.profileDetailsCard p {
  font-size: 16pt;
}

.profileDetailsCard p,
.profileDetailsCard h1,
.profileDetailsCard h2,
.profileDetailsCard h3,
.profileDetailsCard h4,
.profileDetailsCard h5 {
  color: rgb(202, 203, 213);
  font-weight: lighter;
  font-style: normal;
}

.profileDetailsCard h2 {
  padding-left: 0;
}

.feedbackPage {
  position: relative;
  height: 220vh;
  min-height: 100vh;
  width: 100vw;
  min-width: 100vw;
  background-color: var(--cloud);
}

.feedbackHeader {
  margin: auto;
  padding: 1%;
  width: 40vw;
}
.feedbackHeader > h2 {
  padding: 8%;
}

.reviewCards {
  display: flex;
}

.reviewCard {
  width: 17vw;
  height: 35vw;
  margin: 3%;
  margin-right: 4%;
  margin-left: 4%;
  border-radius: 5px;
  background-color: rgba(211, 212, 197, 0.5);
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.03);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.reviewCard:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.08);
}

.reviewCard:first-child {
  margin-left: 16vw;
}

.reviewCard > img {
  width: 60%;
  height: auto;
  margin-top: 12%;
  border-radius: 100%;
}

.reviewQuote {
  padding: 5%;
  padding-right: 10%;
  padding-left: 10%;
  font-style: italic;
}

.starRating {
  background-color: rgba(240, 240, 240, 0.7);
  width: 40%;
  margin: auto;
  padding: 3%;
  padding-top: 4%;
  border-radius: 30px;
}

.starFilled,
.starEmpty {
  width: 1.1vw;
}

.evaluation {
  margin-top: 6%;
}

.futureCard {
  width: 50vw;
  margin-left: 45vh;
  max-width: 1200px;
  height: 49vh;

  position: relative;
  z-index: 10;

  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.03);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.futureCard:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 54vw;
  height: 100%;
  background: var(--lavender);
  -webkit-transform-origin: 100% 100% 0;
  transform-origin: 100% 100% 0;
  -webkit-transform: skew(-4deg);
  transform: skew(-4deg);
  z-index: -1;

  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.03);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.evaluation:hover > img,
.evaluation:hover > .futureCard {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  /* box-shadow: 0 0px 30px rgba(0, 0, 0, 0.08); */
}

.evaluation > img {
  float: left;
  height: 49vh;
  width: auto;
  left: 0;
  z-index: -2;
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.evaluationText {
  width: 45vw;
  margin: auto;
  padding: 6%;
  padding-left: 2%;
  text-align: left;
}

.evaluationText > h3 {
  padding-bottom: 5%;
  font-size: 2.5vw;
}

#inversedEvaluation > .futureCard {
  margin: auto;
  margin-right: 41vh;
}

#inversedEvaluation > .futureCard:after {
  -webkit-transform: skew(4deg);
  transform: skew(4deg);
  background-color: var(--green);
}

#inversedEvaluation > img {
  float: right;
  margin-right: -3%;
}

